<template>
  <svg class="svg-icon" 
  :style="{width: props.width + 'px', height: props.height + 'px', color: props.color}" 
  @mousedown="clickIcon">
    <use :xlink:href="`#icon-${props.name}`" :fill="props.color" />
  </svg>
</template>

<script>
import { defineComponent } from "vue";

export default defineComponent({
  name: "SvgIcon",
  props: {
    name: {
      type: String,
      required: true,
      default: "email"
    },
    width: {
      type: String,
      default: "26"
    },
    height: {
      type: String,
      default: "26"
    },
    color: {
      type: String,
      default: "#000"
    }
  },
  setup(props) {
    return {
      props
    };
  }
});
</script>