<template>
  <div id="app" ref="app">
    <NavBar class="navBar"></NavBar>
    <div class="pageBox">
      <!-- <keep-alive> -->
      <router-view></router-view>
      <!-- </keep-alive> -->
    </div>
    <FooterBar v-if="false"></FooterBar>
  </div>
</template>

<script>
import { ref, watch } from "vue";
import { useRoute } from "vue-router";
import NavBar from "@/components/navbar/NavBar";
import FooterBar from "@/components/footerBar/FooterBar";
export default {
  name: "App",
  components: {
    NavBar,
    FooterBar,
  },
  data() {
    return {};
  },
  setup() {
    const route = useRoute();
    const animation = ref("slide");
    watch(route, () => {
      animation.value = route.meta.index == 1 ? "slide-right" : "slide-left";
    });
    return {
      animation,
    };
  },
  mounted() {
    this.screenWidth(); //監聽視窗寬度的方法
    this.screenHeight();
  },
  methods: {
    screenWidth() {
      //監聽視窗寬度的方法
      this.$store.commit("upDate", { windowSize: document.body.clientWidth });
      window.onresize = () => {
        return (() => {
          window.screenWidth = document.body.clientWidth;
          this.$store.commit("upDate", {
            windowSize: document.body.clientWidth,
          });
        })();
      };
    },
    screenHeight() {
      //監聽視窗高度的方法
      this.$store.commit("upDate", {
        windowHeight: document.body.clientHeight,
      });
      window.onresize = () => {
        return (() => {
          window.screenHeight = document.body.clientHeight;
          this.$store.commit("upDate", {
            windowHeight: document.body.clientHeight,
          });
        })();
      };
    },
  },
  watch: {
    $route() {
      // this.$refs.app.scrollTop = this.$refs.app.scrollHeight - this.$refs.app.clientHeight //滚动条随时在下
      this.$refs.app.scrollTop = 0; //滚动条随时在上
    },
  },
};
</script>

<style scoped>
@import url(./assets/css/base.css);
#app {
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  background: var(--main-bg-color);
  position: relative;
  color: rgb(255, 255, 255);
}
#app::-webkit-scrollbar {
  /*滾動條寬度*/
  width: 8px;
}
#app::-webkit-scrollbar-thumb {
  /*滾動條顏色*/
  background-color: #7e7e7e;
  border-radius: 8px;
}
#app::-webkit-scrollbar-thumb:hover {
  background-color: #616161;
}
.navBar {
  position: fixed;
  top: 0;
  left: 0;
}

/*pageBox */
.pageBox {
  margin-top: 45px;
  width: 100%;
}
</style>
