<template>
  <div id="TextAnimationOne">
    <span class="textBox"
      @mouseenter="mouseTransition(0)" 
      @mouseout="mouseTransition(1)">
      <slot></slot>
    </span>
    <div class="transitionBox">
      <div class="transition" ref="transition"></div>
    </div>
  </div>
</template>

<script>

export default {
  name:'TextAnimationOne',
  components: {

  },
  props:{

  },
  data() {
    return {
      
    }
  },
  methods:{
    mouseTransition(state){
      if(state == 0){
        this.$refs[`transition`].style.width = "100%"
      }else{
        this.$refs[`transition`].style.width = "0%"
      }
    }
  },
}
</script>

<style scoped>
.textBox{
  transition: .3s;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #E6E6E6;
}
.textBox:hover{
  transition: .3s;
  font-size: 16px;
}
.transitionBox{
  width: 100%;
  display: flex;
  justify-content: center;
}
.transition{
  width: 0%;
  transition: .3s;
  border-top: 2px solid rgb(255, 255, 255);
}
</style>
